body {
  margin: 0;
  padding: 0;
  background-color: azure;
}
.ticTacToe {
  text-align: center;
  font-family: cursive;
}
@media (max-width: 1100px) {
  .ticTacToe {
    font-family: system-ui;
    font-weight: bold;
  }
}
.ticTacToe h1 {
  font-size: xxx-large;
}
.ticTacToe .ticTacToeGame {
  width: 300px;
  height: 300px;
  border: 2px solid black;
  margin: 50px auto;
  border-radius: 7px;
}
@media (max-width: 1100px) {
  .ticTacToe .ticTacToeGame {
    width: 700px;
    height: 700px;
    border-radius: 17px;
    margin-bottom: 150px;
  }
}
.ticTacToe .ticTacToeGame .ttt-grid {
  width: 100px;
  height: 100px;
  border: 1px solid black;
  float: left;
  box-sizing: border-box;
  padding: 15px;
  font-size: xxx-large;
  cursor: pointer;
}
@media (max-width: 1100px) {
  .ticTacToe .ticTacToeGame .ttt-grid {
    width: 233px;
    height: 233px;
    padding: 80px;
  }
}
.ticTacToe button {
  padding: 15px 105px;
  border: none;
  background-color: burlywood;
  font-size: larger;
  border-radius: 3px;
  font-family: cursive;
  cursor: pointer;
}
@media (max-width: 1100px) {
  .ticTacToe button {
    font-size: xxx-large;
    padding: 50px 240px;
    border-radius: 13px;
    font-family: system-ui;
    font-weight: bold;
  }
}
.ticTacToe .winner {
  font-size: xxx-large;
}
@media (max-width: 1100px) {
  .ticTacToe .winner {
    font-size: xxx-large;
    margin-bottom: 150px;
  }
}
.ticTacToe footer {
  position: absolute;
  bottom: 10px;
  right: calc(46%);
}
@media (max-width: 1100px) {
  .ticTacToe footer {
    font-size: xx-large;
    margin-top: 355px;
    right: calc(38%);
  }
}
